import { useRef } from "react"
import { Box } from "@mui/material"
import { FILES_FEATURE_NAME } from "@rp/common/settings"
import { UploadInputRef } from "@rp/react/web/common/base-components"
import { UploadFiles } from "@rp/react/web/common/files"
import { HeaderButton } from "@rp/react/web/common/layout"
import { useModal } from "@rp/react/web/common/providers"
import { FileUploadOutlined } from "@rp/react/web/mui/icons"

type UploadFileButtonProps = {
  onSuccess: () => void
  disableUpload: boolean
}

function UploadFileButton({ onSuccess, disableUpload }: UploadFileButtonProps) {
  const ref = useRef<UploadInputRef>(null)
  const {
    planComparisonModal: { handleOpen: planComparisonHandleOpen },
  } = useModal()

  const handleClick = () => {
    disableUpload ? planComparisonHandleOpen(FILES_FEATURE_NAME) : ref.current?.open()
  }

  return (
    <Box sx={{ ml: 2 }}>
      <HeaderButton
        key="header-button"
        onClick={handleClick}
        startIcon={<FileUploadOutlined />}
        variant="contained-light"
      >
        Upload File
        <UploadFiles
          location="filesPage"
          multiple
          ref={ref}
          onSubmit={() => {
            onSuccess()
          }}
        />
      </HeaderButton>
    </Box>
  )
}

export { UploadFileButton }
